.todoItem{
    width: 100%;
    display: grid;
    grid-template-columns: 0.3fr 1fr .3fr;
    grid-template-areas: " a b c";
    justify-content: space-around;
    height: auto;
    max-width: 400px;

    text-align: center;
    align-items: center;
    border: 1px solid var(--white);
    border-radius: 14px;
    margin-top: 6px;
    position: relative;

}
.todoItem:hover{
    background-color: rgb(255, 255, 255,0.1);
}
.todo-text-container{
    grid-area: b;
    height: auto;
    display: block;
    overflow:hidden;
    /* overflow-wrap:break-word; */
    /* white-space: nowrap; */
    /* text-overflow: ellipsis */
}
.todoItem-p{
    font-size: var(--font-size-regular);
    font-style: var(--font-style);
    /* max-width:  */
    max-width:100%;
    overflow-wrap:break-word;
}
.todoItem-p--complete{
   text-decoration: line-through;
}