.buttonToAdd{
    height: 50px;
    width: 50px;
    position: fixed;
    right: 10px;
    bottom:10px ;
    font-size: 50px;
    font-weight: bold;
    background-color:var(--light-blue);
    color: var(--white);
    border: none;
     border-radius: 5px;
     cursor: pointer;
     display: flex;
     justify-content: center;
     align-items: center;
     transform: scale(1);
     transition: .3s ease;
     z-index: 2;
     
}
.buttonToAdd:hover{
    transform:scale(1.2);
}