.form-container{
    cursor:default;
    min-width: 400px;
    width: auto;
    height: auto;
    min-height: 300px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;  
    background-color: var(--white); 
    border: var(--light-blue) 2px solid;
    border-radius: 6px;
    box-shadow: 0px 5px 50px rgba(255, 255, 255, 0.25);
   }
.form-tittle{
    color: var(--black);
    font-size: 24px;
    text-align: center;
    font-weight: normal;
    margin: 0;
   
}
.form-input{
    resize: none;
    background: var(--white);
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0;
    height: 70px;
    width: 90%;
    text-align: center;
    font-size: var(--font-size-regular);
    box-shadow: 0px 5px 50px rgba(32,35,41,0.25);
}
.form-input::placeholder{
    color: var(--gray);
    font-weight: 500;
}

.form-input:hover{
    border:2px solid var(--light-blue) ;
}
.form-input:focus{
    outline-color:#7ee1fd;
}

.form-button-container{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-around;
}

.form-button{

    border-radius: 6px;
    width:90px;
    height: 50px;
    border: none;
    font-size: var(--font-size-regular);

}
.form-button__cancel{
background: transparent;

}
.form-button__add{
    background: var(--light-blue);
    box-shadow: 0px 5px 50px rgba(97,218,250,0.5);
    }
