.list-contain{
    display: flex;
    justify-content: center;
    align-items: center;
    margin:24px 0 0 0;
    padding: 0;
    width: 400px;
    overflow-y: auto;
    overflow-x: hidden;
    height: auto;
   grid-row: 3/4;
   align-self: start;
   justify-self: center;
   
}
.list-contain .list-contain-order{
    list-style: none;
    width: 100%;
}